<template>
  <div id="app">
    <!-- 使用卡片容器和布局 -->
    <el-card class="box-card" shadow="hover" style="width: 100%; max-width: 600px; margin: 20px auto;">
      <div class="card-header">
        <h1 style="font-size: 24px; font-weight: bold; color: #333;">My To-Do List</h1>
        <el-button type="primary" @click="handleAddNewTask" size="medium" icon="el-icon-plus">Add New Task</el-button>
      </div>

      <!-- 使用 ToDoForm 组件 -->
      <to-do-form @todo-added="addToDo"></to-do-form>

      <!-- 分隔线 -->
      <el-divider></el-divider>

      <!-- 列表标题 -->
      <h2 id="list-summary" ref="listSummary" tabindex="-1" style="font-size: 18px; color: #555;">{{ listSummary }}</h2>

      <!-- 使用 Element Plus 输入框，并增加前置图标 -->
      <el-input
        v-model="newToDoLabel"
        placeholder="Enter a new task"
        clearable
        size="large"
        prefix-icon="el-icon-edit"
        class="input-box"
        style="margin-bottom: 20px;"
      ></el-input>

      <!-- 使用 Element Plus 加载效果 -->
      <el-loading :loading="isLoading" spinner="el-icon-loading" text="Loading tasks...">
        <ul aria-labelledby="list-summary" class="stack-large" style="list-style-type: none; padding: 0;">
          <li v-for="item in ToDoItems" :key="item.id">
            <el-card class="todo-item-card" shadow="always" style="margin-bottom: 10px; border-radius: 8px;">
              <div class="todo-item-content" style="display: flex; justify-content: space-between; align-items: center;">
                <div style="display: flex; align-items: center;">
                  <el-checkbox v-model="item.done" @change="updateDoneStatus(item.id)" :label="item.label"></el-checkbox>
                  <span v-if="item.done" style="margin-left: 10px; color: #bbb;">(Completed)</span>
                </div>

                <!-- 任务操作按钮 -->
                <div>
                  <el-button type="text" icon="el-icon-edit" size="small" @click="editToDo(item.id, item.label)" style="margin-right: 10px;"></el-button>
                  <el-button type="text" icon="el-icon-delete" size="small" @click="deleteToDo(item.id)"></el-button>
                </div>
              </div>
            </el-card>
          </li>
        </ul>
      </el-loading>

      <!-- 分页功能 -->
      <el-pagination
        v-if="ToDoItems.length > 10"
        :total="ToDoItems.length"
        :page-size="10"
        layout="prev, pager, next"
        @current-change="handlePageChange"
        style="text-align: center; margin-top: 20px;"
      ></el-pagination>
    </el-card>
  </div>
</template>





<script>
//import ToDoItem from "./components/ToDoItem.vue";
import uniqueId from "lodash.uniqueid";
import ToDoForm from "./components/ToDoForm";
//import { ref } from 'vue';

export default {
  name: "app",
  components: {
    //ToDoItem,
    ToDoForm,
  },
  data() {
    return {
      ToDoItems: [
        { id: uniqueId("todo-"), label: "Learn Vue", done: false },
        { id: uniqueId("todo-"), label: "Create a Vue project with the CLI", done: true },
        { id: uniqueId("todo-"), label: "Have fun", done: true },
        { id: uniqueId("todo-"), label: "Create a to-do list", done: false },
      ],
      newToDoLabel: '', // 新任务标签
    };
  },
  methods: {
    addToDo(toDoLabel) {
      this.ToDoItems.push({ id: uniqueId('todo-'), label: toDoLabel, done: false });
    },
    handleAddNewTask() {
      if (this.newToDoLabel.trim()) {
        this.addToDo(this.newToDoLabel);
        this.newToDoLabel = ''; // 清空输入框
      }
    },
    updateDoneStatus(toDoId) {
      const toDoToUpdate = this.ToDoItems.find(item => item.id === toDoId);
      toDoToUpdate.done = !toDoToUpdate.done;
    },
    deleteToDo(toDoId) {
      const itemIndex = this.ToDoItems.findIndex(item => item.id === toDoId);
      this.ToDoItems.splice(itemIndex, 1);
      this.$refs.listSummary.focus();
    },
    editToDo(toDoId, newLabel) {
      const toDoToEdit = this.ToDoItems.find(item => item.id === toDoId);
      toDoToEdit.label = newLabel;
    },
  },
  computed: {
    listSummary() {
      const numberFinishedItems = this.ToDoItems.filter(item => item.done).length;
      return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`;
    },
  },
};
</script>

